<template>
    <div class="search_area_wrapper">
        <span class="search_area">
            <input type="text" placeholder="검색어를 입력해주세요" v-model="searchTxt">
            <button @click="requestSearch">검색버튼</button>
        </span>			
    </div>
</template>

<script>
export default {
    
  name: 'BoardSearch',
    data(){
        return{
            searchTxt:this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
            board: {
                id: null,
                boardContent:this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
                boardTitle:this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
				page: {
					page: 0,
					size: 10,
					sort: 'regDate,desc',
				}


			},
		}
    },
    methods:{
		requestSearch(){
            this.$router.push({
				path: '/board',
				query: {
					searchTxt: this.searchTxt,
				}
			})

		},
    }
}
</script>