<template>
	<div>
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
				<div class="header_wrap underLineGray">
					<h1 style="padding-left:5%;">게시판</h1>
				</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="contents">
				<table class="messageList">
					<tbody>
						<tr v-for="(board,index) in getBoardList" :key="index" @click="boardDetail(board.id)">
							<td>{{board.boardTitle}}
								<p style="font-family:Lato; font-weight: normal; font-stretch: normal; font-size:13px; ">{{YYYYMMDDDate(board.regDate)}}</p>
							</td>
						</tr>
						<!--<tr> new date 시
						<td >[업데이트] 모바일 부고장 디자인 및 기능 업그레이드 기능 업그레이드 기능 업그레이드 기능 업그레이드<span class="newdata">NEW</span><p>2020.06.03</p></td>
					</tr>-->
					</tbody>
				</table>
				<div class="paging">
					<button v-if="startPage!==1" @click="onPageChange(startPage-1)" class="prev">이전</button>
					<a :class="page===getBoardPage?'on':''" v-for="(page,index) in pages()" :key="index" @click="onPageChange(page)">{{page}}</a>
					<button v-if="getBoardPageTotal>endPage" @click="onPageChange(endPage+1)" class="next">다음</button>
				</div>
			</div>
			<!-- //content -->
			<!-- footer -->
			<Footer/>
			<!-- //footer -->
		</div>
	</div>
</template>

<script>
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'
import Search from '@/components/board/Search'

import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'

export default {
	name: 'ObituaryReg',
	mixins: [myMixin],
	components: {
		Header,
		Footer,
		Search
	},
	props: {},
	data() {
		return {
			searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
			board: {
				id: null,
				boardContent: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
				boardTitle: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
				page: {
					page: this.$route.query.page ? this.$route.query.page : 0,
					size: this.$route.query.size ? this.$route.query.size : 5,
					sort: this.$route.query.sort ? this.$route.query.sort : 'regDate,desc',
				}
			},
			pages: function() {
				const list = [];
				for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
				return list
			},
		}
	},
	computed: {
		...mapGetters(['getBoardList', 'getBoardPage', 'getBoardPageTotal', 'getBoardTotalElements']),
		startPage() {
			return parseInt((this.getBoardPage - 1) / 5) * 5 + 1
		},
		endPage() {
			return (parseInt((this.getBoardPage - 1) / 5) + 1) * 5 < this.getBoardPageTotal ? (parseInt((this.getBoardPage - 1) / 5) + 1) * 5 : this.getBoardPageTotal
		}
	},
	mounted() {
		this.requestGetBoardList()
	},
	beforeDestroy() {
		this.$store.commit('BOARD_INIT')
	},
	methods: {
		boardDetail(id) {
			this.$router.push({
				path: '/boardDetail',
				query: {
					id: id,
				}
			})
		},
		requestGetBoardList() {
			const parameter = {
				board: this.board
			}
			this.$store.dispatch('REQUEST_BOARD_LIST', parameter)
		},
		boardMore() {
			const param = {
				page: this.board.page.page + 1
			}
			this.requestBoardList(param)
		},
		onPageChange(val) {
			const param = {
				page: val
			}
			this.requestBoardList(param)
		},
		requestBoardList(param) {
			let currentPage = 0;
			if (param !== undefined && param !== null &&
				param.page !== undefined && param.page !== null) {
				currentPage = param.page - 1;
			}
			this.$router.push({
				path: '/board',
				query: {
					page: currentPage,
					size: this.board.page.size,
					sort: this.board.page.sort
				}
			})
		},
	}
}
</script>

